import React from 'react';
import { Text } from 'core/src/typography';
import { cssDecorator } from '@front-toolkit/utils';

import styles from './Placeholder.module.scss';

const cd = cssDecorator(styles);

type Status = 'error' | 'loading' | 'retry' | 'notFound';

const statusToPlaceholderText: { [key in Status]: string } = {
  error: 'Не удалось загрузить список. Попробуйте позже.',
  loading: 'Загрузка…',
  retry: 'Загрузка… Возможна задержка, подождите.',
  notFound: 'Ничего не найдено'
};

interface PlaceholderProps {
  /** Статус взаимодействия */
  status: Status;

  /** Флаг: добавлять верхний отступ плейсхолдеру */
  topIndent?: boolean;
}

const Placeholder: React.FC<PlaceholderProps> = ({ topIndent, status }) => (
  <div className={ cd('placeholder', { top_indent: topIndent }) }>
    <Text size="small" color="tertiary">{ statusToPlaceholderText[status] }</Text>
  </div>
);

export default Placeholder;
